.navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    color: var(--white);
    border-bottom: 1px solid var(--grey);
}

.brand-name {
    font-family: var(--font-accent);
    font-size: 1.75rem;
    font-weight: 400;
    line-height: 2.25rem;
    letter-spacing: -0.066rem;
    text-transform: uppercase;
}


.navigation-menu ul {
    display: flex;
    padding: 0;
    margin: 0;
}

.navigation-menu li {
    margin: 0 1rem;
    list-style-type: none;
}

.navigation-menu li a {
    display: block;
    width: 100%;
    text-decoration: none;
    color: var(--grey-lt);
    text-transform: uppercase;
    font-size: var(--fs-11);
    font-weight: var(--fw-bold);
    letter-spacing: 0.0625rem;
    transition: all 0.5s ease-in-out;
}

.navigation-menu li a:hover {
    color: var(--white);
}

.button--hamburger {
    display: none;
    cursor: pointer;
    border: 0;
    background-color: var(--black);
    margin-left: auto;
}

.chevron {
    display: none;
}

@media screen and (max-width: 768px) {
    .button--hamburger {
        display: flex;
        align-items: center;
    }

    .button--hamburger-expanded {
        opacity: 0.25;
    }

    .hamburger__img {
        width: 1.5rem;
    }

    .navigation-menu ul {
        display: none;
    }

    .navigation-menu li {
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .navigation-menu li a {
        padding: 1.5rem 0;
        font-size: var(--fs-15);
        color: var(--white);
    }

    .dot {
        border-radius: 50%;
        padding: 0.625rem 0.625rem;
        margin-right: 1.5rem;
    }

    .chevron {
        display: block;
        padding: 0.5rem;
    }

    .navigation-menu-expanded ul {
        display: block;
        position: absolute;
        top: 60px;
        left: 0;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        padding: 2rem 1.5rem;
        background-color: var(--black);
        z-index: 1;
    }
}