@import url('https://fonts.googleapis.com/css2?family=Antonio:wght@500&family=League+Spartan:wght@400;700&display=swap');

:root {
    --white: #FFFFFF;
    --black: #070724;
    --grey-lt: #838391;
    --grey: #38384F;
    --blue-lt: #419EBB;
    --blue: #2D68F0;
    --orange-: #EDA249;
    --violet: #6F2ED6;
    --red-xlt: #CD5120;
    --red-lt: #D14132;
    --red: #D83A34;
    --green: #1EC2A4;

    --font-primary: "Spartan", sans-serif;
    --font-accent: "Antonio", sans-serif;

    --fs-80: 5rem;
    --fs-40: 2.5rem;
    --fs-15: 0.9375rem;
    --fs-14: 0.875rem;
    --fs-12: 0.75rem;
    --fs-11: 0.6875rem;

    --fw-reg: 400;
    --fw-bold: 700;
}

/* Reset */
*, *::before, *::after {
    box-sizing: border-box;
}

body, h1, h2, h3, h4, p {
    margin: 0;
    padding: 0;
}

/* Utility classes */
.text--upper-white {
    text-transform: uppercase;
    color: var(--white);
}

.text--upper-grey {
    text-transform: uppercase;
    color: var(--grey-lt);
}

.desktop-only {
    margin-right: 0.5rem;
}


body {
    font-family: var(--font-primary);
    background-color: var(--black);
    background-image:url("../public/assets/background-stars.svg");
    color: var(--white);
    min-height: 100vh;
}


@media screen and (max-width: 768px) {
    .desktop-only {
        display: none;
    }
}
