/* Main section */

.main {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-areas: "img name" "img link" "img men";
    place-items: center;
    height: 75vh;
    padding: 0 6rem;
    grid-template-rows: 1.2fr 1.25rem 1fr;
}

.menu {
    font-size: 0.75rem;
    font-weight: var(--fw-bold);
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    color: var(--grey);
    grid-area: men;
    justify-self: stretch;
    align-self: start;
    margin-top: 3rem;
}

.menu-item {
    margin-bottom: 1rem;
    padding: 1rem;
    border: 1px solid var(--grey);
}


.menu-item > p {
    cursor: pointer;
}


.menu-item:last-child span:last-of-type {
    margin-left: 0.5rem;
}

.planet-img {
    grid-area: img;
    /*border-radius: 50%;
    outline: 3px solid greenyellow;*/
    transform: scale(0.7);
}

.text {
    grid-area: name;
    justify-self: start;
    align-self: end;
    margin-bottom: 2.5rem;
}

.text > h2 {
    font-family: var(--font-accent);
    font-size: var(--fs-80);
    margin-bottom: 1.4375rem;

}

.text > p {
    font-size: var(--fs-14);
    line-height: 1.5625rem;
    padding-right: 1rem;
}

.link {
  color: var(--grey-lt);
    grid-area: link;
    justify-self: start;;
}

.link > * {
    text-transform: capitalize;
    padding-right: 0.2rem;
}

.wiki-icon {
    vertical-align: middle;
}

@media screen and (max-width: 768px) {
    .main {
       display: block;
        text-align: center;
        height: initial;
        padding: initial;
    }

    .menu {
        font-size: 0.5625rem;
        flex-direction: row;
        justify-content: space-between;
        padding: 1rem 1.5rem;
        border-bottom: 1px solid var(--grey);
    }

    .menu-item {
        margin-bottom: initial;
        padding: initial;
        border: initial;
    }

    .planet-img {
        display: block;
        margin: 0 auto;
        transform: scale(0.5);
        max-width: 70vw;
    }
    .text {
        padding: 0 1.5rem;
        margin-bottom: 2rem;
    }

    .text > h2 {
        font-size: var(--fs-40);
        margin-bottom: 1rem;
    }

    .text > p {
        font-size: var(--fs-11);
        font-weight: var(--fw-reg);
        line-height: 1.375rem;
        padding: 0 1.4rem;
        margin-bottom: initial;
    }

    .link {
        color: var(--grey-lt);
        line-height: 1.5625rem;
        font-size: 0.75rem;
        margin-bottom: 1.5rem;
    }

    .link > * {
        text-transform: capitalize;
        padding-right: 0.2rem;
    }

    .wiki-icon {
        vertical-align: middle;
        width: 12px;
    }


}


/* Info section */
.infos {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 3rem;
    padding: 0 6rem;
}

.info-container {
    display: flex;
    flex-direction: column;
    width: 15.9375rem;
    padding: 1.5rem;
    border: 1px solid var(--grey);

}

.info-container > h4 {
    font-family: var(--font-primary);
    font-size: 0.6875rem;
    font-weight: var(--fw-bold);

}

.info-container > p {
    font-family: var(--font-accent);
    font-size: var(--fs-40);
    font-weight: var(--fw-reg);
    letter-spacing: -0.09375rem;
}

.wiki {
    text-decoration: none;
    }
@media screen and (max-width: 768px) {
    .infos {
        flex-direction: column;
        margin-bottom: 2.25rem;
        padding: 0 1.5rem;
    }

    .info-container {
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        width: auto;
        margin-bottom: 0.5rem;
        padding: 0.5rem 1.5rem;
    }

    .info-container > p {
        font-size: 24px;
        letter-spacing: -0.046875rem;
    }

    .info-container > h4 {
        font-size: 0.5rem;
    }

    .menu {
        margin-top: 0;
        }

    .menu-item {
        padding: 0.3rem;
        border-radius: 5%;
        }
}